<template>
	<div class="goods">
		<div class="any-flex">
			<el-button type="primary"></el-button>
			<el-button type="success" @click='goaddgoods' v-if="this.global.is_shop()"></el-button>
		</div>

		<el-table :data="tableData" style="width: 100%">
			<el-table-column prop="goods_id" label="ID" width="80">
			</el-table-column>
			<el-table-column prop="shop_name" label="" width="180" v-if="this.global.is_admin()">
			</el-table-column>
			<el-table-column prop="goods_name" label="" width="280">
			</el-table-column>
			<el-table-column prop="price" label="" width="80">
			</el-table-column>
			<el-table-column prop="goods_effect" label="" width="180">
			</el-table-column>

			<el-table-column prop="goods_duration" label="(min)" width="80" v-if="this.global.is_admin()">
			</el-table-column>

			<el-table-column prop="goods_process" label="" width="180" v-if="this.global.is_admin()">
			</el-table-column>

			<el-table-column prop="goods_frequency" label="" width="80" v-if="this.global.is_admin()">
			</el-table-column>

			<el-table-column prop="goods_overdue_time" label="()" width="80" v-if="this.global.is_admin()">
			</el-table-column>

			<el-table-column prop="goods_appointment" label="" width="180" v-if="this.global.is_admin()">
			</el-table-column>


			<el-table-column prop="" label=" " width="180">
				<template slot-scope="scope">
					<el-image style="width: 169px; height: 97px" :src="imgurl + scope.row.picture" :preview-src-list="scope.row.srclist">
					</el-image>
				</template>
			</el-table-column>
			<el-table-column prop="goods_gd_num" label="" width="100">
			</el-table-column>

			<el-table-column prop="" label="" width="280" v-if="this.global.is_shop()">
				<template slot-scope="scope">
					<el-button type="primary" @click='promotion(scope.row)'>{{scope.row.is_promotion == 0 ? '' :''}}</el-button>
					<el-button type="primary" @click='limited_buys(scope.row)'>{{scope.row.limited_buy == 0 ? '' : ''}}</el-button>
				</template>
			</el-table-column>
			<el-table-column prop="" label="" width="280">
				<template slot-scope="scope">
					<el-button type="success" @click='exchgoods(scope.row.goods_id)' v-if="isshop"></el-button>
					<el-button type="danger" @click='deletegoods(scope.row.goods_id,scope.$index)'></el-button>
				</template>
			</el-table-column>

			<!--  -->
			<el-drawer title="" :visible.sync="drawer" direction="ltr" custom-class="demo-drawer" :before-close="handleClose">
				<div class="demo-drawer__content">
					<div style="margin: 20px;"></div>
					<el-form :label-position="labelPosition" label-width="100px" :model="formLabelAlign">
						<el-form-item label="">
							<el-input v-model="formLabelAlign.promotion_price"></el-input>
						</el-form-item>
<!-- 						<el-form-item label="">
							<el-input v-model="formLabelAlign.promotion_prople"></el-input>
						</el-form-item> -->
					</el-form>
					<div class="any-flex" style="justify-content: space-between;margin-top: 50px;">
						<div></div>
						<el-button type="success" @click='goclick'></el-button>
					</div>
				</div>
			</el-drawer>

			<!--  -->
			<el-drawer title="" :visible.sync="drawer1" direction="ltr" custom-class="demo-drawer" :before-close="handleClose">
				<div class="demo-drawer__content">
					<div class="addbooking">
						<div style="margin: 20px;"></div>
						<el-form :label-position="labelPosition" label-width="130px" :model="formLabelAlign1">
							<el-form-item label="">
								<el-input v-model="formLabelAlign1.limited_price"></el-input>
							</el-form-item>
							<el-form-item label="">
								<el-input v-model="formLabelAlign1.limited_prople"></el-input>
							</el-form-item>
							<el-form-item label="">
								<div class="block">
									<el-date-picker v-model="value1" type="datetimerange" range-separator="" start-placeholder=""
									 end-placeholder="">
									</el-date-picker>
								</div>
							</el-form-item>
						</el-form>

						<div class="any-flex" style="justify-content: space-between;margin-top: 50px;">
							<div></div>
							<el-button type="success" @click='addlimit'></el-button>
						</div>
					</div>
				</div>
			</el-drawer>
		</el-table>


		<div class="any-flex pagein">
			<div></div>
			<el-pagination background prev-text='' :total='total' next-text='' layout="prev, pager, next" @current-change='changepages'
			 :current-page='currentPage' :page-size='pagesize'>
			</el-pagination>
		</div>


	</div>
</template>

<script>
	export default {
		name: 'goods',
		data() {
			return {
				//
				drawer1: false,
				value1: [new Date(2019, 11, 10, 10, 10), new Date(2019, 11, 10, 10, 10)],
				formLabelAlign1: {
					limited_prople: '',
					limited_price: ''
				},
				///////-----
				labelPosition: 'left',
				formLabelAlign: {
					promotion_price: '',
					promotion_prople: '1'
				},
				goods_id: '',
				// 
				drawer: false,
				currentPage: 0,
				total: 0,
				pagesize: 0,
				a: this.global.url,
				dialogImageUrl: '',
				dialogVisible: false,
				tableData: [],
				imgurl:this.global.imgurl
			}
		},
		computed: {
			isshop:function(){
				 return this.global.is_shop()
			},
		},
		methods: {
			// 
			deletegoods(e,a){
				
				this.$confirm(', ?', '', {
				          confirmButtonText: '',
				          cancelButtonText: '',
				          type: 'warning'
				        }).then(() => {
							this.utils.post({
								url:'goodsdelete',
								data:{
									goods_id:e
								}
							}).then(res=>{

								this.tableData.splice(a,1)
								
							})
							
				        
				        }).catch(() => {
				          this.$message({
				            type: 'info',
				            message: ''
				          });          
				        });
				
				

			},
			// 
			goaddgoods() {
				this.$router.push({
					path: '/hellow/addgoods',
					query: {
						addgoods: 789,
						// goods_id:''
					}
				});
			},
			// 
			exchgoods(e){
				this.$router.push({
					path: '/hellow/addgoods',
					query: {
						addgoods: 456,
						goods_id:e
					}
				});
			},
			// 
			handleClose(done) {
				done();
				this.formLabelAlign.promotion_price = '',
					this.formLabelAlign.promotion_prople = '1',
					this.goods_id = ''
			},
			//
			addlimit() {
				this.formLabelAlign1.goods_id = this.goods_id,

					this.formLabelAlign1.limited_start_time = new Date(this.value1[0]).getTime()/1000 ,
					this.formLabelAlign1.limited_stop_time = new Date(this.value1[1]).getTime()/1000,
					this.formLabelAlign1.limited_buy = 1

				this.utils.post({
					url: 'admin_Limited',
					data: this.formLabelAlign1
				}).then(res => {
						this.tableData.forEach((item, index) => {
							if (item.goods_id == this.goods_id) {
								if(item.is_promotion == 0){
									item.limited_buy = 1
								}
							}
						})
					
					this.drawer1 = false
				})
			},
			// 
			limited_buys(e) {
				this.goods_id = e.goods_id
				if (e.limited_buy == 0) {
					this.drawer1 = true
				} else if (e.limited_buy == 1) {
					this.utils.post({
						url: 'admin_Limited',
						data: {
							goods_id: this.goods_id,
							limited_buy: 0
						}
					}).then(res => {
						
							this.tableData.forEach((item, index) => {
								if (item.goods_id == this.goods_id) {
									item.limited_buy = 0
								}
							})
						
					})
				}
			},
			// 
			promotion(e) {
				this.goods_id = e.goods_id
				if (e.is_promotion == 0) {
					this.drawer = true
				} else if (e.is_promotion == 1) {
					this.utils.post({
						url: 'admin_Assemble',
						data: {
							is_promotion: 0,
							goods_id: this.goods_id
						}
					}).then(res => {
							this.tableData.forEach((item, index) => {
								if (item.goods_id == this.goods_id) {
									item.is_promotion = 0
								}
							})
					})
				}

			},
			// 
			goclick() {
				
				this.formLabelAlign.goods_id = this.goods_id
				this.formLabelAlign.is_promotion = 1
				this.global.a(this.formLabelAlign)
				this.utils.post({
					url: 'admin_Assemble',
					data: this.formLabelAlign
				}).then(res => {
					this.tableData.forEach((item, index) => {
						if (item.goods_id == this.goods_id) {
							if(item.limited_buy == 0){
								item.is_promotion = 1
							}
						}
					})
					this.drawer = false
				})
			},
			// --- 
			handleRemove(file, fileList) {
				console.log(file, fileList);
			},
			handlePictureCardPreview(file) {
				this.dialogImageUrl = file.url;
				this.dialogVisible = true;
			},
			// 
			init() {
				this.utils.post({
					url: 'goodsList',
					data: {
						shop_id: this.global.shop_id()
					}
				}).then(res => {
					res.data.forEach((item, index) => {
						item.srclist = [this.imgurl + item.picture]
					})
					this.total = res.total
					this.currentPage = res.current_page
					this.pagesize = res.per_page
					this.tableData = res.data
				})
			},
			// 
			changepages(e) {
				this.utils.post({
					url: 'goodsList',
					data: {
						page: e,
						shop_id: this.global.shop_id()
					}
				}).then(res => {
					res.data.forEach((item, index) => {
						item.srclist = [this.imgurl + item.picture]
					})
					this.tableData = res.data
				})

			},
		},
		mounted() {
			this.init()
		}
	}
</script>

<style lang="scss">
	.pagein {
		padding: 20px 90px;
		justify-content: space-between;
	}

	.demo-drawer__content {
		width: 90%;
		margin: 0 auto;
	}
</style>
