<template>
	<div class="home">

		<!--  -->
		<div style="margin-top: 15px;">
			<el-input placeholder="" v-model="input3" class="input-with-select">
				<el-select v-model="select" slot="prepend" placeholder="">
					<el-option label="" value="1"></el-option>
					<el-option label="" value="2"></el-option>
				</el-select>
				<el-button slot="append" @click='getone' icon="el-icon-search"></el-button>
			</el-input>
		</div>

		<div class='some-message'>
			<div class=" io">

				<div class="hd">  </div>
				<div>{{anynum}}</div>

			</div>
			<div class=" io">
				<div class="hd">  </div>
				<div>{{anymoney}}</div>
			</div>
			<div class=" io">
				<div class="hd">  </div>
				<div>{{anyramain}}</div>
			</div>


		</div>

		<!--  -->
		
		
		
		<el-table :data="tableData" style="width: 100%" >
			<el-table-column fixed prop="shop_id" label="ID" width="100">
			</el-table-column>
			<el-table-column fixed prop="shop_name" label="" width="200">
			</el-table-column>
			<el-table-column prop="shop_phone" label="()" width="200">
			</el-table-column>
			<el-table-column prop="shop_contacts" label="" width="100">
			</el-table-column>
			<el-table-column prop="shop_address_provice" label="" width="100">
			</el-table-column>
			<el-table-column prop="shop_address_city" label="" width="100">
			</el-table-column>
			<el-table-column prop="shop_address_area" label="" width="100">
			</el-table-column>
			<el-table-column prop="shop_address_detail" label="" width="120">
			</el-table-column>

			<el-table-column prop="shop_cash" label="" width="120">
			</el-table-column>
			<el-table-column prop="shop_account" label="" width="100">
			</el-table-column>
			<el-table-column prop="" label="" width="300">
				<template slot-scope="scope">
					<el-button type="primary" @click='blockshop(scope.$index, scope.row)'></el-button>
					<el-button type="danger" @click='businessdelete(scope.$index, scope.row)'></el-button>
				</template>
			</el-table-column>

		</el-table>

		<div class="any-flex pagein">
			<div></div>
			<el-pagination background prev-text='' :total='total' next-text='' layout="prev, pager, next" 
			@current-change='changepages'
			:page-count='currentPage'
			:page-siez='pagesize'
			>
			</el-pagination>
		</div>

	</div>
</template>

<script>
	export default {
		name: "home",
		data() {
			return {
				currentPage: 1,
				tableData: [], // 
				input3: '', //
				select: '' ,// 1. 2.
				total:0,
				anynum:0,
				anymoney:0,
				anyramain:0,
				pagesize:0
			}
		},
		methods: {
			// 
			init() {
				this.utils.post({
					url: 'business'
				}).then(res => {
					this.tableData = res.data
					this.total = res.total
					this.pagesize = res.pre_page
				})
			},
			// 
			blockshop(e,a){
				
				this.$confirm(', ?', '', {
				  confirmButtonText: '',
				  cancelButtonText: '',
				  type: 'warning'
				}).then(() => {
					
					this.utils.post({
						url:'businessblack',
						data:{
							shop_id:a.shop_id
						}
					}).then(res=>{
						
						this.tableData.splice(e,1)
						console.log(res)
					})
				  this.$message({
				    type: 'success',
				    message: '!',
				  });
				
				
				
				
				}).catch(() => {
				  this.$message({
				    type: 'info',
				    message: ''
				  });          
				});
				

				
				
			},
			// 
			changepages(e) {
				console.log(e)
				this.utils.post({
					url: 'business',
					data:{ page:e }
				}).then(res => {
					this.tableData = res.data
				})
			},
		
			// 
			getone() {
				if (this.select == '') {
					this.$message.error('');
				} else if (this.select == 1) {
					let shop_phone = ''
					let shop_contacts = this.input3
					this.goon(shop_phone, shop_contacts)
				} else if (this.select == 2) {
					let shop_contacts = ''
					let shop_phone = this.input3
					this.goon(shop_phone, shop_contacts)
				}
			},

			// 
			goon(shop_phone, shop_contacts) {
				this.utils.post({
					url: 'serachbusiness',
					data: {
						shop_phone: shop_phone,
						shop_contacts: shop_contacts
					}
				}).then(res => {
					this.tableData = res
				})
			},
		
			// 
			businessdelete(a,e){
				this.$confirm(', ?', '', {
				          confirmButtonText: '',
				          cancelButtonText: '',
				          type: 'warning'
				        }).then(() => {
							
							this.utils.post({
								url:'businessdelete',
								data:{
									shop_id:e.shop_id
								}
							}).then(res=>{
									this.tableData.splice(a,1)
									this.$message({
									  type: 'success',
									  message: '!'
									});
							})

				        }).catch(() => {
				          this.$message({
				            type: 'info',
				            message: ''
				          });          
				        });	
			},
		
			anyother(){
				// 
				this.utils.post({
					url:'businessman'
				}).then(res=>{
					this.anynum =  res
				})
				
				// 
				this.utils.post({
					url:'businessmoney'
				}).then(res=>{
					this.anymoney =  res
				})
				
				// 
				this.utils.post({
					url:'businessall'
				}).then(res=>{
					this.anyramain =  res
				})
			}
		},
			
		mounted() {
			this.init()
			this.anyother()
		}
	};
</script>

<style lang="scss">
	.pagein {
		padding: 20px 90px;
		justify-content: space-between;
	}

	.some-message {
		margin: 20px 0;
		width: 100%;
		height: 100px;
		background: #72edfb;
		display: flex;
		justify-content: space-between;

		.io {
			width: 20%;
			height: 100%;
			line-height: 50px;
			text-align: center;

			.hd {
				font-weight: 600;
			}
		}
	}



	.el-select .el-input {
		width: 130px;

	}

	.input-with-select .el-input-group__prepend {
		background-color: #fff;
	}
</style>
